.page {
  font-family: Roboto, sans-serif;
}

.page__body {
  margin: 0;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__photo-container {
  justify-content: center;
  padding-bottom: 40px;
  display: flex;
}

.card__photo {
  width: 160px;
  height: 134px;
}

.card__title {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  padding-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__container-reyting {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__stars {
  margin: 0;
  display: flex;
}

.card .star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card__stars .star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__stars .star:last-child {
  margin: 0;
}

.card__reviews {
  align-items: flex-end;
  height: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card .price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card .price__card {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card .price__amount {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button a {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button a:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}
